  transition:all .3s ease;
  LEFT:0

}

.hexagon:before,
.hexagon:after {
  content: "";
  position: absolute;
  width: 0;
  border-left: 150px solid transparent;
  border-right: 150px solid transparent;
}

.hexagon:before {
  bottom: 100%;
  border-bottom: 72.17px solid rgba(100, 170, 202, 0.7);

}

.hexagon:after {
  top: 100%;
  width: 0;
  border-top: 62.17px solid rgba(100, 170, 202, 0.7);
}

